import React, { useEffect, useState } from "react";
import ReactMarkdown from "react-markdown";
import axios from "axios";
import recordMd from "../../assets/docs/record.md";
const Record: React.FC<any> = (props) => {
  const [content, setContent] = useState("");
  useEffect(() => {
    axios
      .request({
        url: recordMd,
        method: "get",
        responseType: "text",
      })
      .then((res) => res.data)
      .then((data) => {
        const regex = /\\n/g;
        // console.log(data.replace(regex, "\n"));
        // const time = "#### \\n#### 2018";
        // console.log(time);

        // console.log(3434);

        // console.log(time);
        // console.log('#### \n#### 2018');

        // console.log(data === "#### \n#### 2018");

        // reader.result  typed array  blob

        // fs.writeFile("test.json", data, () => {});
        // const staticContent = data.replace(regex, "\\");
        // console.log(staticContent);
        setContent(data.replace(regex, "\n")); ///
      });
  }, []);

  return (
    <div className="about-me">
      <ReactMarkdown>{content.toString()}</ReactMarkdown>
    </div>
  );
};
export default Record;
